.footer {   
	padding-top: 48px;
	padding-bottom: 20px;  
	background: #111;  
	font-size: 20px;   
	text-align: center;
 }

.footer .rrss-logo {
	font-size: 50px;
	padding-right: 10px;	
}
.footer .up-arrow {
	font-size: 70px;
	padding-bottom: 10px;
	color: black;	
	transition: all 0.2s ease-in-out;
}
.footer .up-arrow:hover{	
	color: white;	
}


.footer .mapmarker-img {
	width: 30px;
	height: 30px;
}

.footer .love {
	font-size: 16px;
	color:gray;
}
.footer .ubicacion{
	color:gray;
}

#go-top {
	position: absolute;
	top: -80px;
	left: 50%;
}

.icon-up {
	width: 40px;
	height: 40px;
}

#go-top a {
	border: 0 none;
	display: block;
	width: 60px;
	height: 60px;
	background-color: #F7AE00;
	-webkit-transition: all 0.2s ease-in-out;
	-moz-transition: all 0.2s ease-in-out;
	-o-transition: all 0.2s ease-in-out;
	-ms-transition: all 0.2s ease-in-out;
	transition: all 0.2s ease-in-out;

	border-radius: 100%;
}

#go-top a:hover { 
	background-color: gray; 
}