.TimeLine {
  background-color: #ddd;
}

.line-1{
    position: relative;
    top: 50%;  
    width: 24em;
    margin: 0 auto;
    border-right: 3px solid rgba(255,255,255,.75);
    font-size: 270%;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    transform: translateY(-1%);    
}

/* Animation */
.anim-typewriter{
  animation: typewriter 4s steps(40) 1s 1 normal both,
             blinkTextCursor 600ms steps(40) infinite normal;
}
@keyframes typewriter{
  from{width: 0;}
  to{width: 7.5em;}
}
@keyframes blinkTextCursor{
  from{border-right-color: rgba(255,255,255,.75);}
  to{border-right-color: transparent;}
}
#container{
height: 600px;
width: 100%;
background-color: #ddd;
overflow: hidden;
position: relative;
margin-top: 30px;
display: inline-block;
}

#thumbs{
background-color: #ddd;
position: absolute;
top: 0px;
left: 0px;
height: 100%;
width: auto;
overflow: hidden;
white-space: nowrap;
padding: 30px 100px;
}

.history-block {
  height: 100%;
  width: 300px;
  background-color: #cdcdcd;
  margin: 0 -2px;
  border-right: 1px solid #fff;
  display: inline-block;
  font-family: 'roboto condensed', sans-serif;
  font-size:60px;
  font-weight: 100;
  color: #FFF;
  cursor: pointer;
  box-shadow: 2px 5px 20px rgba(0,0,0,0.8);
  transition: width 0.2s;
}

.history-block:last-of-type {
  border-right: none;
}

.cover {
  width: 100%;
  height: 100%;
  transition: background .5s;
  background: rgba(0,0,0,0.6)
}

.cover:hover {
  width: 100%;
  height: 100%;
  transition: background .5s;
  background: rgba(0,0,0,0.2);
}

.year {
  position: absolute;
  top: 0px;
  font-size: 50%;
  font-weight:500;
  margin: 40px 30px;
}

.title {
  position: absolute;
  display: block;
  width: 260px;
  font-size: 25%;
  margin: 30px;
  top: 60px;
  white-space: normal;
  transition: width 0.2s;
}



