@media only screen and (max-width:600px){
  #container{
  height: 600px;
  width: 100%;
  background-color: #ddd;
  overflow: hidden;
  position: relative;
  }
  
  #thumbs{
  background-color: #ddd;
  position: absolute;
  top: 0px;
  left: -100px;
  height: 100%;
  width: auto;
  overflow: hidden;
  white-space: nowrap;
  
  }
  .history-block{
    height: 100%;
    width: 100px;
    background-color: #cdcdcd;
    border-right: 1px solid #fff;
    font-family: 'roboto condensed', sans-serif;
    font-size:60px;
    font-weight: 100;
    color: #FFF;
    cursor: pointer;
    box-shadow: 2px 5px 20px rgba(0,0,0,0.8);
    transition: width 0.2s;
  }
  .year {
    position: absolute;
    top: 0px;
    font-size: 40%;
    font-weight:500;
    margin: 40px 30px;
  }
  
  .title {
    position: absolute;
    display: inline-block;
    width: 40px;
    font-size: 25%;
    margin: 30px;
    top: 60px;
    white-space: normal;
    transition: width 0.2s;
  }
}