}

.display{
    position: relative;
    height: 100vh;
    min-height: 25rem;
    width: 100%;
    z-index: 2;
    overflow: hidden;
}



.Video {
    position: absolute;
    top: 50%;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    z-index: 0;
    transform: translateX(-50%) translateY(-50%);
}



.display-cards{
