#go-top {
	position: absolute;
	top: -80px;
	left: 50%;
}

.icon-up {
	width: 40px;
	height: 40px;
}

#go-top a {
	border: 0 none;
	display: block;
	width: 60px;
	height: 60px;
	background-color: #F7AE00;
	transition: all 0.2s ease-in-out;

	border-radius: 100%;
}

#go-top a:hover { 
	background-color: gray; 
}
