@media only screen and (max-width:600px){
  #container{
  height: 600px;
  width: 100%;
  background-color: #ddd;
  overflow: hidden;
  position: relative;
  }
  
  #thumbs{
  background-color: #ddd;
  position: absolute;
  top: 0px;
  left: -100px;
  height: 100%;
  width: auto;
  overflow: hidden;
  white-space: nowrap;
  
  }
  .history-block{
    height: 100%;
    width: 100px;
    background-color: #cdcdcd;
    border-right: 1px solid #fff;
    font-family: 'roboto condensed', sans-serif;
    font-size:60px;
    font-weight: 100;
    color: #FFF;
    cursor: pointer;
    box-shadow: 2px 5px 20px rgba(0,0,0,0.8);
    transition: width 0.2s;
  }
  .year {
    position: absolute;
    top: 0px;
    font-size: 40%;
    font-weight:500;
    margin: 40px 30px;
  }
  
  .title {
    position: absolute;
    display: inline-block;
    width: 40px;
    font-size: 25%;
    margin: 30px;
    top: 60px;
    white-space: normal;
    transition: width 0.2s;
  }
}

.titleTour{
    position: relative;
    top: 50%;  
    width: 24em;
    margin: 0 auto;
    border-right: 3px solid rgba(255,255,255,.75);
    font-size: 270%;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    transform: translateY(-1%);    
}

/* Animation */
.anim-typewriter-Tour{
  animation: typewriterTour 9s steps(60) 9s 1 normal both,
             blinkTextCursor 600ms steps(40) infinite normal;
}
@keyframes typewriterTour{
  from{width: 0;}
  to{width: 20.5em;}
}
@keyframes blinkTextCursor{
  from{border-right-color: rgba(255,255,255,.75);}
  to{border-right-color: transparent;}
}

@media (max-width: 768px){
    .titleTour{
        font-size: 4vw;
    }
}

.container-team {
    background-color: #16384c;
    box-shadow: 0 5px 20px rgba(0,0,0,0.5);
    height: 80vh;
}

.container-team .card {
    width: 310px;
    height: 300px;
    background: #fff;
    margin: 10px;
    box-shadow: 0 5px 20px rgba(0,0,0,0.5);
    transition: 0.3s ease-in-out;
    
    text-align: center;
}

.container-team .card:hover{
    height: 450px;
    box-shadow: 0 5px 20px rgba(0,0,0,0.5);
}

.container-team .card .imgBx{
    position: relative;
    padding: 20px;
    top: -60px;
    z-index: 1;
}

.container-team .card .imgBx img{
    width: 260px;
    height: 260px;
    border-radius: 4px;  
    box-shadow: 0 5px 20px rgba(0,0,0,0.5);  
}

.container-team .card .title{
    margin-top: 180px;
    color: #111;
    visibility: visible;
    opacity: 1;
    transition-delay: 0.3s;
}

.container-team .card .send-email {
    font-size: 40px;
}

.container-team .card .content{
    position: relative;
    margin-top: -20px;
    padding: 10px 15px;
    text-align: center;
    color: #111;
    visibility: hidden;
    opacity: 0;
    transition: 0.1s ease-in-out;
}

.container-team .card:hover .content{
    visibility: visible;
    opacity: 1;
    margin-top: -10px;
    transition-delay: 0.3s;
}
