
.container-team {
    background-color: #16384c;
    box-shadow: 0 5px 20px rgba(0,0,0,0.5);
    height: 80vh;
}

.container-team .card {
    width: 310px;
    height: 300px;
    background: #fff;
    margin: 10px;
    box-shadow: 0 5px 20px rgba(0,0,0,0.5);
    transition: 0.3s ease-in-out;
    
    text-align: center;
}

.container-team .card:hover{
    height: 450px;
    box-shadow: 0 5px 20px rgba(0,0,0,0.5);
}

.container-team .card .imgBx{
    position: relative;
    padding: 20px;
    top: -60px;
    z-index: 1;
}

.container-team .card .imgBx img{
    width: 260px;
    height: 260px;
    border-radius: 4px;  
    box-shadow: 0 5px 20px rgba(0,0,0,0.5);  
}

.container-team .card .title{
    margin-top: 180px;
    color: #111;
    visibility: visible;
    opacity: 1;
    transition-delay: 0.3s;
}

.container-team .card .send-email {
    font-size: 40px;
}

.container-team .card .content{
    position: relative;
    margin-top: -20px;
    padding: 10px 15px;
    text-align: center;
    color: #111;
    visibility: hidden;
    opacity: 0;
    transition: 0.1s ease-in-out;
}

.container-team .card:hover .content{
    visibility: visible;
    opacity: 1;
    margin-top: -10px;
    transition-delay: 0.3s;
}